export default {
	data() {
		return {
			open_id: ''
		}
	},
	computed: {
		logoImage() {
			return this.$store.state.config.shop_logo
		}
	},
	onLoad() {
		const _this = this

		this.getCode(function(code) {
			var data = {
				code: code
			}
			_this.$api.login1(data, (res) => {
				if (!res.status) {
					_this.$common.successToShow(res.msg, function() {
						uni.navigateBack({
							delta: 1
						})
					})
				} else {
					_this.open_id = res.data.openid
				}
			})
		})

	},

	// onUnload: function() {
	//   wx.reLaunch({
	//     url: '/pages/index/index'
	//   })
	// },

	methods: {
		getCode: function(callback) {
			uni.login({



				success: function(res) {
					if (res.code) {
						return callback(res.code)
					} else {
						//login成功，但是没有取到code
						this.$common.errorToShow('未取得code')
					}
				},
				fail: function(res) {
					this.$common.errorToShow('用户授权失败wx.login')
				}
			})
		},
		handleRefuse() {
			uni.showToast({
				title: '未授权',
				icon: 'none',
				duration: 1000,
			})
			setTimeout(() => {
				uni.hideToast();
				uni.navigateBack(-1);
			}, 1000);
		},
		getUserInfo: function(e) {
			//console.log(e);
			let _this = this
			//return false;
			if (e.detail.errMsg == 'getUserInfo:fail auth deny') {
				_this.$common.errorToShow('未授权')
			} else {
				var data = {
					open_id: _this.open_id,
					iv: e.detail.iv,
					edata: e.detail.encryptedData,
					signature: e.detail.signature
				}
				//有推荐码的话，带上
				var invitecode = _this.$db.get('invitecode')
				if (invitecode) {
					data.invitecode = invitecode
				}
				_this.toLogin(data)
			}
		},
		//实际的去登陆
		toLogin: function(data) {
			let _this = this
			_this.$api.login2(data, function(res) {
				if (res.status) {
					//登陆成功，设置token，并返回上一页
					_this.$db.set('userToken', res.data)
					uni.navigateBack({
						delta: 1
					})
					return false
				} else {
					uni.redirectTo({
						url: '/pages/login/login/index?user_wx_id=' + _this.open_id
					})
				}
				// if (res.status) {
				//   //判断是否返回了token，如果没有，就说明没有绑定账号，跳转到绑定页面
				//   if (typeof res.data.token == 'undefined') {
				//     uni.redirectTo({
				//       url: '/pages/login/login/index?user_wx_id=' + res.data.user_wx_id
				//     })
				//   } else {
				//     //登陆成功，设置token，并返回上一页
				//     _this.$db.set('userToken', res.data)
				//     uni.navigateBack({
				//       delta: 1
				//     })
				//     return false
				//   }
				// } else {
				//   _this.$common.errorToShow('登录失败，请重试')
				// }
			})
		},



















































































































	}
}
